@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Raleway:200,300,400,500,600);
body {
  overflow: auto;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Anton', sans-serif; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.cities {
  padding: 10px; }

.text-center {
  text-align: center !important;
  top: 30%;
  left: 30%; }

h1 {
  font-size: calc(1.375rem + 1.5vw); }

.column {
  float: left;
  width: 15%;
  height: 30%;
  padding: 0 10px; }

.Cards {
  display: flex;
  justify-content: center; }

.media {
  padding: 10px;
  width: 200px;
  margin: auto; }

.card {
  margin-bottom: 10px;
  margin-top: 5px; }

.contener {
  display: grid;
  justify-content: space-around;
  grid-template-columns: 380px 1fr; }

.map {
  margin: 10px;
  width: 60vw;
  height: 90vh; }

.mapcard {
  width: 100%;
  margin: 10px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  height: 90vh;
  overflow: auto; }

#header {
  background-image: url(https://s3.eu-west-3.amazonaws.com/sport-finder.image/images/cache/full_hd/background/matteo-paganelli-48129-dark.jpg); }

#header {
  background-color: #323232;
  text-align: left;
  height: auto;
  background-attachment: scroll;
  background-size: cover;
  background-origin: padding-box;
  background-origin: initial;
  background-clip: border-box;
  background-clip: initial;
  background-position-x: 50%;
  background-position-y: 60%; }

#header {
  color: #fff;
  display: block; }

#header-content {
  min-height: 100px;
  min-height: 100px;
  height: calc(100vh - 250px);
  display: flex;
  align-items: center;
  justify-content: center;
  background-attachment: fixed; }

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.Image {
  padding: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
  border: 1px solid #000; }

.Deta {
  padding: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
  border: 1px solid #000; }

.Des {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #000; }

/* Inner */
.inner {
  margin: 0 auto;
  width: 75rem;
  max-width: calc(100% - 6rem); }

@media screen and (max-width: 480px) {
  .inner {
    max-width: calc(100% - 3rem); } }

#banner {
  align-items: center;
  display: flex;
  background-image: url("https://www.baptisthealthfoundation.org/wp-content/uploads/2017/09/bolo-bash-golf-feature-1.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-top: 0;
  min-height: 25rem;
  border-radius: 0 0 85% 85% / 30%; }

h1 {
  font-size: 67px;
  color: #ffffffe0;
  text-align: center; }

.img-slide {
  height: 400px; }

.content {
  font-size: 30px;
  border-radius: 4px;
  height: 100%;
  padding: 3rem;
  text-align: center;
  padding: 2rem;
  background: #ffffff;
  box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.025);
  display: -moz-flex;
  display: -ms-flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  border-radius: 4px;
  height: 100%;
  padding: 5rem; }

p {
  text-align: center;
  font-size: 15px; }

.site-footer {
  background-color: #26272b;
  padding: 40px 0 20px;
  font-size: 15px;
  line-height: 24px;
  color: #737373; }

.site-footer hr {
  border-top-color: #bbb;
  opacity: 0.5; }

.site-footer hr.small {
  margin: 20px 0; }

.site-footer h6 {
  color: #fff;
  font-size: 16px;
  text-transform: uppercase;
  margin-top: 5px;
  letter-spacing: 2px;
  text-align: center; }

.site-footer a {
  color: #737373; }

.site-footer a:hover {
  color: #3366cc;
  text-decoration: none; }

.footer-links {
  padding-left: 0;
  list-style: none; }

.footer-links li {
  display: block; }

.footer-links a {
  color: #737373; }

.footer-links a:active, .footer-links a:focus, .footer-links a:hover {
  color: #3366cc;
  text-decoration: none; }

.footer-links.inline li {
  display: inline-block; }

.site-footer .social-icons {
  text-align: right; }

.site-footer .social-icons a {
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-left: 6px;
  margin-right: 0;
  border-radius: 100%;
  background-color: #33353d; }

.copyright-text {
  margin: 0; }

@media (max-width: 991px) {
  .site-footer [class^=col-] {
    margin-bottom: 30px; } }

@media (max-width: 767px) {
  .site-footer {
    padding-bottom: 0; }
  .site-footer .copyright-text, .site-footer .social-icons {
    text-align: center; } }

.social-icons {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none; }

.social-icons li {
  display: inline-block;
  margin-bottom: 4px; }

.social-icons li.title {
  margin-right: 15px;
  text-transform: uppercase;
  color: #96a2b2;
  font-weight: 700;
  font-size: 13px; }

.social-icons a {
  background-color: #eceeef;
  color: #818a91;
  font-size: 16px;
  display: inline-block;
  line-height: 44px;
  width: 44px;
  height: 44px;
  text-align: center;
  margin-right: 8px;
  border-radius: 100%;
  -webkit-transition: all .2s linear;
  transition: all .2s linear; }

.social-icons a:active, .social-icons a:focus, .social-icons a:hover {
  color: #fff;
  background-color: #29aafe; }

.social-icons.size-sm a {
  line-height: 34px;
  height: 34px;
  width: 34px;
  font-size: 14px; }

.social-icons a.facebook:hover {
  background-color: #3b5998; }

.social-icons a.twitter:hover {
  background-color: #00aced; }

.social-icons a.linkedin:hover {
  background-color: #007bb6; }

.social-icons a.dribbble:hover {
  background-color: #ea4c89; }

@media (max-width: 767px) {
  .social-icons li.title {
    display: block;
    margin-right: 0;
    font-weight: 600; } }

